
* {
  box-sizing: border-box;
}
html, body {
  margin: 0;
  padding: 0;
}
body {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #0000ff;
  color: #ffffff;
}
.header > div {
  text-align: center;
}
.filter {
  display: inline-block;
}
.newsfeed {
  margin-top: 6rem;
}




